.wcb-slider__wrap {
	padding: 32px;

	.slick-dots li {
		margin: 0px;
	}
	.slick-dots li button:before {
		font-size: 8px;
	}

	.slick-prev,
	.slick-next {
		z-index: 3;
		color: black;
		padding: 0.5rem;
		width: unset;
		height: unset;
		&:hover {
			color: black;
		}
		&::before {
			content: "";
			display: none;
		}
	}
}

.wcb-slider__item-inner {
	width: 100%;
}

.wcb-slider-child__wrap {
	width: 100%;
	margin: auto;
}

.wcb-slider__item {
	position: relative;
	.wcb-slider__item-background {
		position: relative;
	}
	.wcb-slider__item-wrap-inner {
		display: flex;
		margin: auto;
	}
}

.wcb-slider__item-inner {
	margin: 0 auto;
	margin-left: auto;
	margin-right: auto;
}

.wcb-slider__item-image {
	display: inline-flex;
	align-items: flex-start;
	flex-shrink: 0;
}

.wcb-slider__item-user {
	display: inline-flex;
	align-items: center;
}

.wcb-slider__wrap-items {
	padding-top: 1rem;
	padding-bottom: 1rem;
}